body {
 background-color:rgb(201, 195, 195);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  
}
h1{
  color: rgb(27, 202, 27);
 
}
.deleteBtn{
  margin-left: 10px;
  background-color:rgb(255, 0, 0,.74);
  border:none;
  padding:0.15rem .32rem;
  border-radius: 15%;
}
.deleteBtn:hover{
  border:2px solid #ff0000ce;
  background-color: #ff0000b4;
}
.addBtn{
  margin-left: 10px;
  background-color:rgba(65, 245, 10, 0.842);
  border:2px solid #4CAF50;
  padding:0.25rem .32rem;
  border-radius: 15%;
}
.addBtn:hover{
  border:2px solid rgba(65, 245, 10, 0.842);
  background-color: #4CAF50;
  transition-duration: 10ms;
}
.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
.message {
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
.persons {
  color: blue;
  padding-top: 5px;
  font-size: 15px;
}
